<template>
    <div class="award-record">
        <div class="empty-tip" v-if="list.length === 0">你还没有领取任何奖励</div>
        <div class="award-list">
            <div class="award-item" v-bind:key="i" v-for="(item, i) in list">
                <div class="item-title">{{item.title}}</div>
                <div class="item-text">{{item.desc}}</div>
                <div class="item-time">{{item.time}}</div>
            </div>
        </div>
    </div>
</template>

<script>
    import API from '../server/api'

    export default {
        data() {
            return {
                list: []
            }
        },
        created() {
            this.fetchRecordList(true);
        },
        mounted() {
            this.log({
                cid: 'newer_task_record',
                aid: 'index',
            });
        },
        methods: {
            async fetchRecordList() {
                const {data} = await this.$http.get(API.newTask('rewardList'));
                if (data) {
                    this.list = data;
                }
            }
        }
    }
</script>

